













import Vue, {PropOptions} from 'vue';
import GridView from '@/configurator/components/grid-view/GridView.vue';
import {GridViewElement, GridViewGroup, toGridViewElements} from '@/configurator/components/grid-view/-utils/GridViewHelper';
import {RapiTagForUi} from '@roomle/web-sdk/lib/definitions/typings/rapi-types';
import {GridViewUiState} from '@/common/store/collection-view-state';

export default Vue.extend({

  components: {
    GridView,
  },

  props: {
    objects: Object as PropOptions<RapiTagForUi>,
    isExpanded: Boolean,
    onExpand: Function,
    onCollapse: Function,
    state: Object as PropOptions<GridViewUiState>,
    isDesktop: Boolean,
  },

  data() {
    return {
      groups: [] as RapiTagForUi[],
    };
  },

  computed: {
    allElements(): GridViewElement[] {
      return toGridViewElements(this.objects.items, 'id');
    },
    normalizedGroups(): GridViewGroup[] {
      const id = this.objects.id as string;
      return [{id, label: this.objects.label as string, elements: this.allElements}];
    },
  },

  methods: {
    async onSelect(possibleChild: GridViewElement) {
      const api = await this.$sdkConnector.plannerApi;
      api.insertItems([(possibleChild.payload as any)]);
    },
  },

});

