















import Vue, {VueConstructor} from 'vue';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
// @ts-ignore
import SvgPlaceholder from '@/common/svgs/placeholder.svg';

export default (Vue as VueConstructor<Vue & {
  $refs: {
    image: HTMLImageElement,
  },
}>).extend({
  components: {
    SvgIcon,
  },

  props: {
    src: String,
    alt: String,
  },

  data() {
    return {
      loadingError: false,
    };
  },

  mounted() {
    this._setupErrorHandler();
  },

  updated() {
    if (!!this.src && this.loadingError) {
      this.loadingError = false;
    }
    this._setupErrorHandler();
  },

  destroyed() {
    const image = this.image;
    if (!image) {
      return;
    }
    image.onerror = () => undefined;
  },

  computed: {
    icons(): object {
      return {
        SvgPlaceholder,
      };
    },
    image(): HTMLImageElement {
      return this.$refs.image;
    },
    hasImage(): boolean {
      return !this.loadingError && !!this.src;
    },
  },
  methods: {
    _setupErrorHandler() {
      const image = this.image;
      if (!image) {
        return;
      }
      image.onerror = () => this.loadingError = true;
    },
  },
});
