

























import Vue, {PropOptions} from 'vue';
// @ts-ignore
import SvgMagnifier from '@/common/svgs/magnifier.svg';
// @ts-ignore
import SvgClose from '@/common/svgs/close.svg';
import {throttle} from '@/common/utils/helper';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
import {GridViewUiState, GRID_VIEW_STATE_MUTATIONS} from '@/common/store/collection-view-state';
export default Vue.extend({

  components: {
    SvgIcon,
  },

  beforeDestroy() {
    this.$store.commit(GRID_VIEW_STATE_MUTATIONS.SET_USER_INITIATED_SEARCH, {state: this.state, value: false});
  },

  props: {
    state: Object as PropOptions<GridViewUiState>,
    isDesktop: Boolean,
  },

  computed: {
    icons(): object {
      return {
        SvgMagnifier,
        SvgClose,
      };
    },
  },

  methods: {
    onInput(event: Event) {
      throttle(() => this.$store.commit(GRID_VIEW_STATE_MUTATIONS.SET_SEARCH, {state: this.state, value: (event.target as HTMLInputElement).value}))();
    },
    onFocus() {
      const input = this.$el.querySelector<HTMLInputElement>('input')!;
      if (!this.state.search) {
        input.focus();
      } else {
        this.$store.commit(GRID_VIEW_STATE_MUTATIONS.SET_SEARCH, {state: this.state, value: null});
        input.focus();
      }
    },
    onCursorInInput() {
      this.$store.commit(GRID_VIEW_STATE_MUTATIONS.SET_USER_INITIATED_SEARCH, {state: this.state, value: true});
    },
    onCursorLeavesInput() {
      this.$store.commit(GRID_VIEW_STATE_MUTATIONS.SET_USER_INITIATED_SEARCH, {state: this.state, value: false});
    },
  },

  destroyed() {
    this.$store.commit(GRID_VIEW_STATE_MUTATIONS.SET_USER_INITIATED_SEARCH, {state: this.state, value: false});
    this.$store.commit(GRID_VIEW_STATE_MUTATIONS.SET_SEARCH, {state: this.state, value: null});
  },

});
