






















import Vue from 'vue';
import ArrowButton from '@/configurator/components/utils/ArrowButton.vue';
import {UI_STATE_ACTIONS} from '@/configurator/store/ui-state';
// @ts-ignore
// import styles because getComputedStyle is very expensive
// ts-ignore because there is a bug with css-modules in vue cli and typescript
import {interactionTiming} from '@/configurator/components/utils/-utils/InteractionContainer.module.scss';
// @ts-ignore
// import styles because getComputedStyle is very expensive
// ts-ignore because there is a bug with css-modules in vue cli and typescript
import {SceneOffsetResizer} from '@/configurator/components/utils/-utils/SceneOffsetResizer.ts';
import {animationDurationInMsToFloat} from '@/common/utils/dom';
import {CanvasOffset} from '@roomle/web-sdk/lib/definitions/common-core/src/common-interfaces';
import {Nullable} from '@/common/utils/types';
import {throttle} from '@/common/utils/helper';
import {GlobalAPI} from '@roomle/web-sdk/lib/definitions/common-core/src/main';
import {SharedUiState} from '@/common/store';
import {PLANNER_UI_STATE_ACTIONS} from '@/planner/store/planner-ui-state';

export default Vue.extend({
  name: 'InteractionsContainer.vue',

  props: {
    environment: {
      type: String,
      default: 'configurator',
    },
  },

  components: {
    ArrowButton,
  },

  data() {
    return {
      _resizer: null as Nullable<SceneOffsetResizer>,
    };
  },

  methods: {
    toggle() {
      if (this.environment === 'planner') {
        this.$store.dispatch(PLANNER_UI_STATE_ACTIONS.SET_INTERACTIONS_EXPANDED, !this.sharedUiState.interactionsExpanded);
      } else {
        this.$store.dispatch(UI_STATE_ACTIONS.SET_INTERACTIONS_EXPANDED, !this.sharedUiState.interactionsExpanded);
      }
    },

    _getCameraOffset(): CanvasOffset {
      const {isDesktop, isLandscape} = this.sharedUiState;
      const groups = this.$store.state.coreData?.groups ? this.$store.state.coreData?.groups : [];
      const parentWidth = this.$el.parentElement!.clientWidth;
      const width = this.$el.clientWidth;
      const parentHeight = this.$el.parentElement!.clientHeight;
      const height = this.$el.clientHeight;
      return SceneOffsetResizer.getCameraOffset(parentWidth, width, parentHeight, height,
        this.isFullscreen, this.isExpanded, isDesktop, isLandscape, groups?.length > 0);
    },
    _handleCameraOffset(..._args: any[]) {
      return; // just here to override it later
    },
  },

  async mounted() {
    let api: GlobalAPI;
    if (this.environment === 'planner') {
      api = await this.$sdkConnector.plannerApi;
    } else {
      api = await this.$sdkConnector.configuratorApi;
    }
    this._resizer = new SceneOffsetResizer(api);
    this._handleCameraOffset = throttle(() => {
      this._resizer?.animateOffset(this._getCameraOffset(), 0);
    }).bind(this);
    this._handleCameraOffset();
    window.addEventListener('resize', this._handleCameraOffset);
  },

  beforeDestroy() {
    window.removeEventListener('resize', this._handleCameraOffset);
  },

  computed: {
    sharedUiState(): SharedUiState {
      if (this.environment === 'planner') {
        return this.$store.state.plannerUiState;
      } else {
        return this.$store.state.uiState;
      }
    },
    isDesktop(): boolean {
      return this.sharedUiState.isDesktop;
    },
    isExpanded(): boolean {
      return this.sharedUiState.interactionsExpanded && !this.sharedUiState.isFullscreen;
    },
    isFullscreen(): boolean {
      return this.sharedUiState.isFullscreen;
    },
  },

  watch: {
    isExpanded: {
      async handler() {
        this._resizer?.animateOffset(this._getCameraOffset(), animationDurationInMsToFloat(interactionTiming));
      },
    },
    isFullscreen: {
      async handler() {
        this._resizer?.animateOffset(this._getCameraOffset(), animationDurationInMsToFloat(interactionTiming));
      },
    },
  },
});
