



























import Vue, {PropOptions} from 'vue';
import SearchInput from '@/configurator/components/utils/SearchInput.vue';
// @ts-ignore
import SvgDots from '@/common/svgs/dots.svg';
// @ts-ignore
import SvgList from '@/common/svgs/list.svg';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
import {GridViewUiState, GRID_VIEW_STATE_MUTATIONS} from '@/common/store/collection-view-state';
export default Vue.extend({

  components: {
    SvgIcon,
    SearchInput,
  },

  computed: {
    icons(): object {
      return {
        SvgList,
        SvgDots,
      };
    },
  },

  props: {
    state: Object as PropOptions<GridViewUiState>,
    isDesktop: Boolean,
  },

  methods: {
    toggleListDisplayType(switchToList: boolean) {
      if (this.state.showAllAsList === switchToList) {
        return;
      }
      this.$store.commit(GRID_VIEW_STATE_MUTATIONS.TOGGLE_LIST_DISPLAY_TYPE, {state: this.state});
    },
  },

});
