













import Vue from 'vue';
import {Nullable} from '@/common/utils/types';

export default Vue.extend({
  props: {
    value: String,
    label: String,
    onSelect: Function,
    isBoolean: Boolean,
    selected: Boolean,
  },

  computed: {
    valideOption(): boolean {
      return !!this.text;
    },
    text(): Nullable<string> {
      if (this.label) {
        return this.label;
      }
      if (this.value !== null && this.value !== undefined) {
        return this.value + '';
      }
      return null;
    },
  },

});
